import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  OnDestroy,
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  Hideable,
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
  PaymentFormFieldHooks,
  PaymentBaseFields,
  RemittanceInfoFields,
} from '@backbase/payment-orders-ang';
import { Observable } from 'rxjs';
import { pluck } from 'rxjs/operators';
import { triggerHook } from '../../helpers/hooks-helpers';

@Component({
  selector: 'bb-payord-header-ui',
  templateUrl: './payord-header.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PayordHeaderComponent implements OnInit, OnDestroy {
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig & Hideable;
  group!: FormGroup;
  headerAddition$?: Observable<string>;

  ngOnInit() {
    const remittanceInfoForm = this.group.parent?.get(
      PaymentBaseFields.remittanceInfo
    );
    this.headerAddition$ = remittanceInfoForm?.valueChanges.pipe(
      pluck(RemittanceInfoFields.amountOptionLabel)
    );

    triggerHook(PaymentFormFieldHooks.onInit, this);
  }

  ngOnDestroy() {
    triggerHook(PaymentFormFieldHooks.onDestroy, this);
  }
}
